import React, { useState } from "react";
import "./App.css";
import Axios from "axios";

function App() {
  const [segment, setSegment] = useState("");
  const [fName, setfName] = useState("");
  const [lName, setlName] = useState("");
  const [gender, setGender] = useState("");
  const [age, setAge] = useState("");
  const [accName, setAccName] = useState("");
  const [city, setCity] = useState("");
  const [state, setState] = useState("");
  const url = "https://webhook.site/da5c0a60-0e10-46cb-961d-11b4e6df60ea";
  const addSegment = () => {
    var Values = document.getElementById("values-container");
    Values.style.display = "block";
  };
  const saveSegment = () => {
    const obj_one = {
      first_name: fName,
      last_name: lName,
      gender: gender,
      age: age,
      account_name: accName,
      city: city,
      state: state,
    };

    const schema = [obj_one];
    Axios.post(url, {
      segment_name: segment,
      schema,
    }).then((res) => {
      console.log(res.data);
    });
    alert("Segment saved successfully");
    window.location.reload();
  };

  return (
    <div className="container ">
      {/* Save Segment Button */}
      <button
        className="btn btn-outline-success mt-3"
        type="button"
        data-bs-toggle="offcanvas"
        data-bs-target="#offcanvasRight"
        aria-controls="offcanvasRight"
      >
        Save Segment
      </button>
      {/* Offcanvas Content */}
      <div
        className="offcanvas offcanvas-end"
        tabIndex="-1"
        id="offcanvasRight"
        aria-labelledby="offcanvasRightLabel"
      >
        <div className="d-flex px-2 py-3" id="title">
          <i
            className="bx bx-chevron-left  fs-3"
            data-bs-dismiss="offcanvas"
          ></i>
          <h5 id="offcanvasRightLabel" className="mb-0">
            Saving Segment
          </h5>
        </div>
        {/* Offcanvas Body */}
        <div className="offcanvas-body">
          {/* Input Box */}
          <div className="mb-3">
            <label for="formGroupExampleInput" className="form-label ">
              Enter the Name of the Segment
            </label>
            <input
              type="text"
              className="form-control rounded-0"
              id="formGroupExampleInput"
              placeholder="Name of the Segment"
              value={segment}
              autoComplete="off"
              onChange={(e) => setSegment(e.target.value)}
            ></input>
          </div>
          <div className="d-flex justify-content-end mb-3">
            <div className="align-items-center d-flex justify-content-center">
              <span className="green-dot  me-2"></span>
              <div className="font-size-12 me-3">- User Traits</div>
            </div>
            <div className="align-items-center d-flex justify-content-center">
              <span className="red-dot  me-2"></span>
              <span className="font-size-12">- Group Traits</span>
            </div>
          </div>
          {/* Values from entered data */}
          <div
            id="values-container"
            className="p-2"
            style={{ display: "none", border: "2px solid #03a9f4" }}
          >
            {/* First Name */}
            {fName.length >= 0 && (
              <div className="accordion accordion-flush border  " id="fir-name">
                <div className="accordion-item">
                  <h2 className="accordion-header" id="flush-headingOne">
                    <button
                      className="accordion-button collapsed  p-2"
                      type="button"
                      data-bs-toggle="collapse"
                      data-bs-target="#first-name"
                      aria-expanded="false"
                      aria-controls="first-name"
                    >
                      First Name
                    </button>
                  </h2>
                  <div
                    id="first-name"
                    className="accordion-collapse collapse"
                    aria-labelledby="flush-headingOne"
                    data-bs-parent="#accordionFlushExample"
                  >
                    <div className="accordion-body px-2 py-1">
                      <input
                        type="text"
                        className="form-control rounded-0"
                        id="firstName"
                        placeholder="Enter the First Name"
                        autoComplete="off"
                        value={fName}
                        onChange={(e) => setfName(e.target.value)}
                      ></input>
                    </div>
                  </div>
                </div>
              </div>
            )}
            {/* Last Name */}
            {lName.length >= 0 && (
              <div
                className="accordion accordion-flush border mt-3 "
                id="las-name"
              >
                <div className="accordion-item">
                  <h2 className="accordion-header" id="flush-headingOne">
                    <button
                      className="accordion-button collapsed  p-2"
                      type="button"
                      data-bs-toggle="collapse"
                      data-bs-target="#last-name"
                      aria-expanded="false"
                      aria-controls="last-name"
                    >
                      Last Name
                    </button>
                  </h2>
                  <div
                    id="last-name"
                    className="accordion-collapse collapse"
                    aria-labelledby="flush-headingOne"
                    data-bs-parent="#accordionFlushExample"
                  >
                    <div className="accordion-body px-2 py-1">
                      <input
                        type="text"
                        className="form-control rounded-0"
                        id="firstName"
                        placeholder="Enter the First Name"
                        autoComplete="off"
                        value={lName}
                        onChange={(e) => setlName(e.target.value)}
                      ></input>
                    </div>
                  </div>
                </div>
              </div>
            )}
            {/* Gender */}
            {gender.length >= 0 && (
              <div
                className="accordion accordion-flush border mt-3 "
                id="fname"
              >
                <div className="accordion-item">
                  <h2 className="accordion-header" id="flush-headingOne">
                    <button
                      className="accordion-button collapsed  p-2"
                      type="button"
                      data-bs-toggle="collapse"
                      data-bs-target="#accGender"
                      aria-expanded="false"
                      aria-controls="accGender"
                    >
                      Gender
                    </button>
                  </h2>
                  <div
                    id="accGender"
                    className="accordion-collapse collapse"
                    aria-labelledby="flush-headingOne"
                    data-bs-parent="#accordionFlushExample"
                  >
                    <div className="accordion-body px-2 py-1">
                      <select
                        id="gender"
                        className="form-select"
                        placeholder="Select here"
                        value={gender}
                        onChange={(e) => setGender(e.target.value)}
                      >
                        <option>Select</option>
                        <option>Male</option>
                        <option>Female</option>
                        <option>Other</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
            )}
            {/* Age */}
            {age.length >= 0 && (
              <div
                className="accordion accordion-flush border mt-3 "
                id="fname"
              >
                <div className="accordion-item">
                  <h2 className="accordion-header" id="flush-headingOne">
                    <button
                      className="accordion-button collapsed  p-2"
                      type="button"
                      data-bs-toggle="collapse"
                      data-bs-target="#accAge"
                      aria-expanded="false"
                      aria-controls="accAge"
                    >
                      Age
                    </button>
                  </h2>
                  <div
                    id="accAge"
                    className="accordion-collapse collapse"
                    aria-labelledby="flush-headingOne"
                    data-bs-parent="#accordionFlushExample"
                  >
                    <div className="accordion-body px-2 py-1">
                      <input
                        type="number"
                        className="form-control rounded-0"
                        id="age"
                        placeholder="Enter your Age"
                        autoComplete="off"
                        value={age}
                        onChange={(e) => setAge(e.target.value)}
                      ></input>
                    </div>
                  </div>
                </div>
              </div>
            )}
            {/* Account Name */}
            {accName.length >= 0 && (
              <div
                className="accordion accordion-flush border mt-3 "
                id="fname"
              >
                <div className="accordion-item">
                  <h2 className="accordion-header" id="flush-headingOne">
                    <button
                      className="accordion-button collapsed  p-2"
                      type="button"
                      data-bs-toggle="collapse"
                      data-bs-target="#accName"
                      aria-expanded="false"
                      aria-controls="accName"
                    >
                      Account Name
                    </button>
                  </h2>
                  <div
                    id="accName"
                    className="accordion-collapse collapse"
                    aria-labelledby="flush-headingOne"
                    data-bs-parent="#accordionFlushExample"
                  >
                    <div className="accordion-body px-2 py-1">
                      <input
                        type="text"
                        className="form-control rounded-0"
                        id="accountName"
                        placeholder="Enter your Account Name"
                        autoComplete="off"
                        value={accName}
                        onChange={(e) => setAccName(e.target.value)}
                      ></input>
                    </div>
                  </div>
                </div>
              </div>
            )}
            {/* City */}

            {city.length >= 0 && (
              <div
                className="accordion accordion-flush border mt-3 "
                id="fname"
              >
                <div className="accordion-item">
                  <h2 className="accordion-header" id="flush-headingOne">
                    <button
                      className="accordion-button collapsed  p-2"
                      type="button"
                      data-bs-toggle="collapse"
                      data-bs-target="#accCity"
                      aria-expanded="false"
                      aria-controls="accCity"
                    >
                      City
                    </button>
                  </h2>
                  <div
                    id="accCity"
                    className="accordion-collapse collapse"
                    aria-labelledby="flush-headingOne"
                    data-bs-parent="#accordionFlushExample"
                  >
                    <div className="accordion-body px-2 py-1">
                      <input
                        type="text"
                        className="form-control rounded-0"
                        id="city"
                        placeholder="Enter your City"
                        autoComplete="off"
                        value={city}
                        onChange={(e) => setCity(e.target.value)}
                      ></input>
                    </div>
                  </div>
                </div>
              </div>
            )}
            {/* State */}
            {state.length >= 0 && (
              <div
                className="accordion accordion-flush border mt-3 "
                id="fname"
              >
                <div className="accordion-item">
                  <h2 className="accordion-header" id="flush-headingOne">
                    <button
                      className="accordion-button collapsed  p-2"
                      type="button"
                      data-bs-toggle="collapse"
                      data-bs-target="#accState"
                      aria-expanded="false"
                      aria-controls="accState"
                    >
                      State
                    </button>
                  </h2>
                  <div
                    id="accState"
                    className="accordion-collapse collapse"
                    aria-labelledby="flush-headingOne"
                    data-bs-parent="#accordionFlushExample"
                  >
                    <div className="accordion-body px-2 py-1">
                      <input
                        type="text"
                        className="form-control rounded-0"
                        id="state"
                        placeholder="Enter your state"
                        autoComplete="off"
                        value={state}
                        onChange={(e) => setState(e.target.value)}
                      ></input>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>

          {/* Input Elements starts here */}
          <div
            className="accordion accordion-flush border mt-3"
            id="accordionFlushExample"
          >
            <div className="accordion-item">
              <h2 className="accordion-header" id="flush-headingOne">
                <button
                  className="accordion-button collapsed  p-2"
                  type="button"
                  data-bs-toggle="collapse"
                  data-bs-target="#flush-collapseOne"
                  aria-expanded="false"
                  aria-controls="flush-collapseOne"
                >
                  Add schema to segment
                </button>
              </h2>
              <div
                id="flush-collapseOne"
                className="accordion-collapse collapse"
                aria-labelledby="flush-headingOne"
                data-bs-parent="#accordionFlushExample"
              >
                <div className="accordion-body px-2">
                  <form>
                    <label for="firstName" className="form-label ">
                      First Name
                    </label>
                    <input
                      type="text"
                      className="form-control rounded-0"
                      id="firstName"
                      placeholder="Enter the First Name"
                      autoComplete="off"
                      value={fName}
                      onChange={(e) => setfName(e.target.value)}
                    ></input>
                    <label for="lastName" className="form-label ">
                      Last Name
                    </label>
                    <input
                      type="text"
                      className="form-control rounded-0"
                      id="lastName"
                      placeholder="Enter the Last Name"
                      autoComplete="off"
                      value={lName}
                      onChange={(e) => setlName(e.target.value)}
                    ></input>
                    <label for="gender" className="form-label">
                      Gender
                    </label>
                    <select
                      id="gender"
                      className="form-select"
                      placeholder="Select here"
                      value={gender}
                      onChange={(e) => setGender(e.target.value)}
                    >
                      <option>Select</option>
                      <option>Male</option>
                      <option>Female</option>
                      <option>Other</option>
                    </select>
                    <label for="age" className="form-label ">
                      Age
                    </label>
                    <input
                      type="number"
                      className="form-control rounded-0"
                      id="age"
                      placeholder="Enter your Age"
                      autoComplete="off"
                      value={age}
                      onChange={(e) => setAge(e.target.value)}
                    ></input>
                    <label for="accountName" className="form-label ">
                      Account Name
                    </label>
                    <input
                      type="text"
                      className="form-control rounded-0"
                      id="accountName"
                      placeholder="Enter your Account Name"
                      autoComplete="off"
                      value={accName}
                      onChange={(e) => setAccName(e.target.value)}
                    ></input>
                    <label for="city" className="form-label ">
                      City
                    </label>
                    <input
                      type="text"
                      className="form-control rounded-0"
                      id="city"
                      placeholder="Enter your City"
                      autoComplete="off"
                      value={city}
                      onChange={(e) => setCity(e.target.value)}
                    ></input>
                    <label for="state" className="form-label ">
                      State
                    </label>
                    <input
                      type="text"
                      className="form-control rounded-0"
                      id="state"
                      placeholder="Enter your state"
                      autoComplete="off"
                      value={state}
                      onChange={(e) => setState(e.target.value)}
                    ></input>
                  </form>

                  <div className="mt-3">
                    <h6
                      className="font-size-12 primary-clr cursor-pointer"
                      onClick={addSegment}
                    >
                      + Add new schema
                    </h6>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="modal-footer p-3 d-flex justify-content-start border-top">
          <button
            className="btn btn-success me-2 "
            onClick={saveSegment}
            data-bs-dismiss="offcanvas"
          >
            Save the Segment
          </button>
          <button
            className="btn btn-outline-danger"
            data-bs-dismiss="offcanvas"
          >
            Cancel
          </button>
        </div>
      </div>
    </div>
  );
}

export default App;
